;(function(window, $, undefined){


	$(document).ready(function(){

		var cont = $('#cursor')

		var isPressed = false

		$(document).on('mousedown touchstart', function(e){

			var p = (e.originalEvent.touches) ? e.touches[0] : e
			var x = p.clientX - 25
			var y = p.clientY - 25
			
			cont.css({x:x, y:y, opacity:.4})

			isPressed=true
		})

		$(document).on('mouseup touchend', function(e){

			cont.transition({opacity:0}, 250)

			isPressed=false
		})

		$(document).on('mousemove touchmove', function(e){

			var p = (e.originalEvent.touches) ? e.touches[0] : e
			var x = p.clientX - 25
			var y = p.clientY - 25

			cont.css({x:x, y:y, opacity:.4})
		})
		
	})


})(window, jQuery)