;(function ($, APP) {
  APP.bar = {}

  $(document).ready(function () {
    var cont = $('#bar').css({y: -100})
    var val = cont.find('.time_bar span')
    var bar = cont.find('.time .time_bar')
    var tgt = cont.find('#tgtIcon')

    var scaleCol1 = d3.scaleLinear()
            .domain([0, 100])
            .range(['#E41D19', '#59BC60'])

    var scaleCol2 = d3.scaleLinear()
            .domain([0, 100])
            .range(['#FB5724', '#4CBAC2'])

    cont.find('[js_abort]').on('click', function () {
            // APP.stateman.go('play.ask')

            // if (confirm( APP.labels.dialogbox )) {
                // APP.stateman.go('home')
      window.location.reload(true)
            // }
      return false
    })

    var icon = cont.find('.icon_wrapper .icon')
    var done = cont.find('.icon_wrapper .done')
    var fail = cont.find('.icon_wrapper .fail')

    function show () {
      cont.transition({y: 0}, 500)
    }

    function hide () {
      cont.transition({y: -100}, 1000)
    }

    function setTime (v, p) {
      val.text(v)
      bar.css('width', p + '%')

      if (p < 30) {
        // val.css('color', scaleCol1(p))
        bar.css('background', 'linear-gradient(90deg, ' + scaleCol2(p) + ', ' + scaleCol1(p) + ')')
      } else {
        // val.css('color', scaleCol1(100))
        bar.css('background', 'linear-gradient(90deg, ' + scaleCol2(100) + ', ' + scaleCol1(100) + ')')
      }
    }

    function setTarget (id) {
      var targets = icon.find('svg > g')
      targets.hide()

      var itm = targets.get(id)
      $(itm).show()

      setBarIcon(0)
    }

    function signalTarget () {
      TweenMax.killTweensOf(tgt)
      TweenMax.set(tgt, {scale: 0.01})
      TweenMax.to(tgt, 1, {scale: 1.0, delay: 1, ease: Elastic.easeInOut})
    }

    function setBarIcon (status) {
      icon.hide()
      done.hide()
      fail.hide()

      if (status === 0) icon.show()
      if (status === 1) done.show()
      if (status === 2) fail.show()
    }

    function setNum (n) {
      var nn = (n < 10) ? '0' + n : n
      cont.find('[js_num_items]').text(nn)
    }

    function setNumTot (n) {
      cont.find('[js_num_total]').text('/' + n)
    }

    APP.bar.signalTarget = signalTarget
    APP.bar.setNum = setNum
    APP.bar.setNumTot = setNumTot
    APP.bar.setBarIcon = setBarIcon
    APP.bar.setTarget = setTarget
    APP.bar.setTime = setTime
    APP.bar.hide = hide
    APP.bar.show = show

    if (APP.version === undefined) show()
  })
})(window.jQuery, window.APP)
