;(function ($, APP) {
  APP.home = {}

  $(document).ready(function () {
    var cont = $('#home')
    var logo = cont.find('.top')
    var p = cont.find('[seq]').css({opacity: 0})

    function show () {
      cont.show()

      logo.css({opacity: 1})

      cont.removeClass('bypass')

      cont.transition({opacity: 1}, 500)

      p.each(function (i, e) {
        $(this).transition({opacity: 1, delay: i * 200 + 500}, 1000)
      })
    }

    function hide () {
      p.each(function (i, e) {
        $(this).transition({opacity: 0}, 1000)
      })
      logo.css({opacity: 0})
      cont.addClass('bypass')

      cont.transition({opacity: 0,
        complete: function () {
          cont.hide()
        }}, 500)
    }

    APP.home.hide = hide
    APP.home.show = show
  })
})(window.jQuery, window.APP)
