;(function(window, $, undefined){

	APP.back = {}

	function init(){

		var cont = $('#back')
		var front = $('#front')
		var svg = $('#back svg')

		var onClicked
		var disabled = false

		var inTransit = false
		var elements = svg.find('#uomini > image')

		elements.on('click', function(e){
			onClicked()
	    })

        var width_view = $(window).width()
        var width_content = $('#back').width()
        var width_content_front = $('#front').width()
        var rapp = width_content_front/width_content

        console.log(width_view, width_content, width_content_front)

        new Impetus({
             source:'#back',
             boundX:[(width_content-width_view)*-1, 0],
             bounce:true,
             initialValues: [(width_content-width_view)/2*-1, 0],
             update: function (x, y) {
                if(!inTransit && !disabled){
                	cont.css({x:x})
                	front.css({x:x*1.65})
                } 
             }
        })



        function restore(){
        	var width_view = $(window).width()
        	var width_content = $('#back').width()
			cont.css({x:(width_content-width_view)*-1})
			front.css({x:(width_content_front-width_view)*-1})
			disabled=false
        }


        function begin(picked){

        	elements.css('display', 'none')
			$(elements.get(picked)).css('display', 'block')
			disabled=false

        	inTransit=true

        	if($('html').hasClass('portrait')){
	        	front.transition({x:(width_content_front-width_view)/2*-1, delay:100}, 2500, 'easeInOutQuart')
	        	cont.transition({x:(width_content-width_view)/2*-1, delay:100, complete:function(){
	        		inTransit=false
	        	}}, 2500, 'easeInOutQuart')
	        }
        }


        function pick(picked){
        	elements.css('display', 'none')
			$(elements.get(picked)).css('display', 'block')
        }



		function show(){
			cont.show()
		}

		function hide(){
			cont.hide()
		}


		APP.back.onClicked = function(clb){
			onClicked = clb
		}

		APP.back.disable = function(clb){
			disabled = true
		}


		APP.back.elements = elements
		APP.back.restore = restore
		APP.back.begin = begin
		APP.back.pick = pick
		APP.back.hide = hide
		APP.back.show = show
		
	}

	APP.back.init = init

})(window, jQuery)