;(function ($, APP) {
  function _PlayState () {
    return {
      enter: function (option) {
        console.log('PlayState :: enter')

        APP.game.start()

        APP.game.onTick(function (v, p) {
          APP.bar.setTime(v, p)
        })

        APP.game.onOver(function () {
          APP.catch.setPoints(APP.game.getPoints())
          APP.stateman.go('play.over')
        })

        APP.back.onClicked(function () {
          if (APP.game.over) return // enforcing concurrency

          APP.game.addPoints()
          APP.catch.setPoints(APP.game.getPoints())

          if (APP.game.getLength() === 0) {
            APP.stateman.go('play.win')
          } else {
            APP.stateman.go('play.catch')
          }
        })
      },
      leave: function (option) {
        console.log('PlayState :: leave')

        APP.game.abort()
      }
    }
  }
  window.PlayState = _PlayState
})(window.jQuery, window.APP)
