;(function ($, APP) {
  function _OverState () {
    return {
      enter: function (option) {
        console.log('OverState :: enter')

        $('#fume').transition({opacity: 1}, 500)

        APP.panel.setAction(APP.labels.gameover_panel_label)

        var dato = APP.getDato()
        console.log(dato)

        APP.catch.setTitle(dato.you_found)
        APP.catch.setText(dato.description)
        APP.catch.setAct(APP.labels.gameover_label)
        APP.catch.setLabel(APP.labels.gameover_mainlabel, '#41B8E6')
        APP.catch.showTextOnly()
        APP.catch.setLink('submit/add?status=over')
        APP.catch.show()

            // // APP.panel.setItemsNum( APP.game.getItems() )

        APP.bar.setBarIcon(2)
        APP.bar.hide()

        APP.back.disable()
      },
      leave: function (option) {
        console.log('OverState :: leave')
        APP.catch.hide()
        $('#fume').transition({opacity: 0}, 500)
      }
    }
  }
  window.OverState = _OverState
})(window.jQuery, window.APP)
